<template>
  <div>
    <!-- 顶部导航 -->
    <van-nav-bar
      @click-left="Return"
      :right-text="isMange?'完成':'管理'"
      @click-right="isCheck"
      left-arrow
      title="确认地址"
      class="bgc"
    ></van-nav-bar>

    <div class="addresList">
      <van-address-list
        v-model="aid"
        :list="addressList"
        default-tag-text="默认"
        @add="onAdd"
        @edit="onEdit"
        :add-button-text="isMange?'删除地址':'新增地址'"
      ></van-address-list>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      aid: "1",

      // 地址列表数据
      addressList: [],

      // 数据管理
      isMange: false
    };
  },

  created() {
    this.getAddressData();
  },

  methods: {
    // 获取地址数据
    getAddressData() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      let tokenString = localStorage.getItem("__Tk");

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 20000) {
            // 添加地址信息
            res.data.result.forEach((v, i) => {
              let currentAddress = {
                id: v.aid,
                tel: v.tel,
                name: v.name,
                address: v.province + v.city + v.county + v.addressDetail,
                isDefault: Boolean(v.isDefault)
              };

              // 判断哪一个地址信息设为默认地址
              if (v.isDefault) {
                this.aid = v.aid;
              }
              // 添加至地址列表中
              this.addressList.push(currentAddress);

              if (!v.isDefault) {
                this.aid = this.addressList[0].id;
                // 
              }
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 管理操作
    isCheck() {
      this.isMange = !this.isMange;
    },

    // 新增地址||删除地址
    onAdd() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,

        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      let tokenString = localStorage.getItem("__Tk");

      // 删除地址
      if (this.isMange) {
        if (this.addressList.length == 0) {
          this.$toast("没有要删除的数据");
        }

        this.addressList.forEach((v, i) => {
          // 删除地址
          if (v.id == this.aid) {
            this.axios({
              method: "POST",
              url: "/deleteAddress",
              data: {
                appkey: this.appkey,
                tokenString,
                aid: v.id
              }
            })
              .then(res => {
                this.$toast.clear();
                if (res.data.code == 10000) {
                  this.addressList.splice(i, 1);
                }
              })
              .catch(err => {
                this.$toast.clear();
                
              });
          } else {
            this.$toast("请选择要删除的地址");
            
          }
        });
      } else {
        this.$toast.clear();

        this.$router.push({ name: "Addres" });
      }
    },

    // 编辑地址
    onEdit(address) {
      this.$toast("编辑地址");
      let o = {
        name: "Addres"
      };
      // 传递aip
      if (address) {
        o.params = {
          aid: address.id
        };
      }

      this.$router.push(o);
    },

    // 返回上一页面并传递数据
    Return() {
      // 确认订单
      this.addressList.forEach(v => {
        if (v.id == this.aid) {
          let obj = {
            name: "Shopcart",
            params: { aid: v.id }
          };
          this.$router.push(obj);
        }
      });
      if (this.addressList.length == 0) {
        this.$router.go(-1);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.bgc {
  background-color: #2c1907;
  position: fixed;
  width: 100%;
}
/deep/.van-icon {
  color: #fff;
  font-size: 18px;
}
/deep/ .van-nav-bar__title,
/deep/.van-nav-bar__text {
  color: #fff;
}

.addresList {
  position: relative;
  top: 50px;

  /deep/.van-icon {
    color: #969799;
    font-size: 18px;
  }

  /deep/.van-icon-success {
    color: #fff;
  }
  /deep/.van-radio__icon--checked {
    /deep/.van-icon-success {
      color: #fff;
      background-color: #2c1907;
      border: 1px solid #2c1907;
    }
  }
  /deep/.van-button--danger,
  /deep/.van-tag--danger {
    border: 1px solid #2c1907;
    background-color: #2c1907;
  }
}
</style>